<template>
<div class="tab-control">
<div v-for="(item,index) in titles" class="tab-control-item" :class="{active:index===currentindex}"
	 @click="itemclick(index)">
	<span>{{item}}</span>
</div>
</div>
</template>

<script>
	export default {
		name:"TabControl",
		 props: {
            titles:{
                type:Array,
                default() {
                    return []
                }
            }
        },
        data(){
        	return{
        	currentindex:0
        	}
        },
        methods:{
        	itemclick(index){
        		this.currentindex=index;
        		this.$emit('tabclick',index)
        	}
        }
	}
</script>

<style scoped="scoped">
.tab-control{
	display:flex;
	text-align: center;
	font-size: 15px;
	height: 40px;
	line-height: 40px;
	
		
}	
.tab-control-item{
	flex: 1;
	
}
.active{
	color: red;
	
}
.active span{
	border-bottom: 3px solid deeppink;
	padding: 5px;
}	
</style>