<template>
   <div id="home">
  	<nav-bar class="home-nav"><div slot="center">头博的衣柜</div></nav-bar>
  	<tab-control :titles="['流行','新品','精选']"
  	 		 @tabclick="tabclick" ref="tabControl1"
  	 		 class="tab-control"
  	 		 v-show="isTabFixed"
  	 		 ></tab-control>
  	<scroll class="content" ref="scroll" 
  		:probe-type="3" @scroll="contentscroll"
  		:pull-up-load="true" @pullingUp="loadmore"> 
  		<home-swiper :banners="banners" @swiperImageLoad="swiperImageLoad"/>
  	 <recommend-view :recommends="recommends"></recommend-view>
  	 <feature-view></feature-view>
  	 	<tab-control :titles="['流行','新品','精选']"
  	 		 @tabclick="tabclick" ref="tabControl2"
  	 		 ></tab-control>
  	 	<goods-list :goods="goods[cttype].list"></goods-list>
  	</scroll>
  	<backtop @click.native="backtopclick" v-show="backtopindex"></backtop>
   </div>
</template>

<script>
	import NavBar from "components/common/navbar/NavBar";
 	import Scroll from "components/common/scroll/Scroll" ;
	import TabControl from "components/contents/tabcontrol/TabControl";
	import GoodsList from "components/contents/goods/GoodsList";
	import Backtop from "components/contents/backtop/Backtop"
	
	import HomeSwiper from "./childComps/HomeSwiper";
	import RecommendView from "./childComps/RecommendView";
	import FeatureView from "./childComps/FeatureView";
	
	import {debounce} from "common/utils"  
	import {getHomeMultiData,getHomeGoods} from "network/home";
    export default {
        name: "Home",
        components:{ 
       	NavBar,
       	HomeSwiper,
       	RecommendView,
       	FeatureView,
       	TabControl,
       	GoodsList,
        Scroll,
        Backtop
       
       },
       data() {
       	return{
       		banners:[],
       		recommends:[],
       		goods:{
       		       'pop':{page:0, list:[]},
                   'new':{page:0, list:[]},
                   'sell':{page:0, list:[]}
            },
            cttype:'pop',
            backtopindex:'true',
            tabOffsetTop:0,
            isTabFixed:false,
            saveY:0
            }
       },
       activated(){
       	this.$refs.scroll.scrolltop(0,this.saveY,0)
       	this.$refs.scroll.refresh()
       },
       deactivated(){
       this.saveY=this.$refs.scroll.getY()
       },
      //created一般只放函数的调用，具体方法的实现放在methods里面
       created() {
       	//1.请求多个数据
       	this.getHomeMultiData()
       	//2.请求商品数据
       	this.getHomeGoods('pop');
       	this.getHomeGoods('new');
       	this.getHomeGoods('sell');
       	
       },
       mounted(){
       	//3.监听item中图片数据加载完成,denounce防抖函数
       	const refresh =debounce(this.$refs.scroll.refresh,50);
       	this.$bus.$on('homeitemimageload',()=>{
       	
       		refresh()
       	})
       },
       methods:{
       	getHomeMultiData(){
       			getHomeMultiData().then(res=>{
       		this.banners=res.data.banner.list;
       		this.recommends=res.data.recommend.list;
       		})
       	},
       	getHomeGoods(type){
       		const page=this.goods[type].page+1;
       		getHomeGoods(type,page).then(res=>{
       			this.goods[type].list.push(...res.data.list);
       			this.goods[type].page+=1;
       		//完成上拉加载函数
       		this.$refs.scroll.finishPullUp()
       		})
       	},
       	tabclick(index){
       		switch(index) {
       			case 0: 
       			this.cttype ='pop'
       			break
       			case 1:
       			this.cttype ='new'
       			break
       			case 2:
       			this.cttype ='sell'
       			break
       		}
       		this.$refs.tabControl1.currentindex=index
       		this.$refs.tabControl2.currentindex=index
       	},
       	backtopclick(){
       		this.$refs.scroll.scrolltop(0,0,2000)
       	},
       	contentscroll(Position){
       		//1.判断backtop是否显示
       		this.backtopindex=(-Position.y)>1000
       		//2.决定tabcontrol是否吸顶
       		 this.isTabFixed=(-Position.y)>this.tabOffsetTop
       	},
        loadmore(){
        	//cttype代表pop，sell，new，告诉 我们加载哪一个
        	this.getHomeGoods(this.cttype)	
        },
        swiperImageLoad(){
        	this.tabOffsetTop=this.$refs.tabControl2.$el.offsetTop
        },
       }
    }
</script>

<style scoped>
	#home{
		/*padding-top: 44px;*/
		height: 100vh;
		position: relative;
	}
.home-nav{
	background-color: var(--color-tint);
	color: #fff;

	/*position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: 9;*/
}

.content{
	
	overflow: hidden;
	position: absolute;
	top: 44px;
	bottom: 49px;
	right: 0;
	left: 0;
}
.tab-control{
	position: relative;
	z-index:9;
	background-color: white;
}
</style>
